import React from 'react'
import _ from 'lodash'
import axios from 'axios'
import { Container, Row, Col } from 'reactstrap'
import config from '../../config'
import Sidebar from './Sidebar/index'
import Content from './Content/index'

class Home extends React.Component {
  state = {
    sites: [],
    activeSourceSite: '',
    products: []
  }
  async componentDidMount() {
    const sites = await this.fetchSites()
    const activeSourceSite = _.get(sites, '0.id')
    const products = await this.fetchProducts(activeSourceSite)
    this.setState({ sites, activeSourceSite, products })
  }
  fetchSites = async () => {
    const url = config.apiUrl + '/sources'
    const response = await axios.get(url)
    return _.get(response, 'data.items')
  }
  fetchProducts = async (sourceSite) => {
    const url = config.apiUrl + '/product_launches?size=10&sourceSite=' + sourceSite
    const response = await axios.get(url)
    return _.get(response, 'data.items')
  }
  onSiteClick = (activeSourceSite) => async () => {
    if (this.state.activeSourceSite !== activeSourceSite) {
      const products = await this.fetchProducts(activeSourceSite)
      this.setState({ activeSourceSite, products })
    }
  }
  render() {
    const { sites, activeSourceSite, products } = this.state
    return (
      <Container style={{ maxWidth: '1024px' }}>
        <Row noGutters>
          <Col className="no-gutters" sm="4">
            <Sidebar
              items={sites}
              onItemClick={this.onSiteClick}
              activeItemId={activeSourceSite}
            />
          </Col>
          <Col className="no-gutters" sm="8">
            <Content items={products} />
          </Col>
        </Row>
      </Container>
    )
  }
}

export default Home
